import styled from "styled-components";

export const LoginDiv = styled.div`
  margin: auto;
  position: absolute;
  top: 250px;
  left: 250px;
`;

export const ErrorMsg = styled.span`
  color: red;
  font: bold;
  font-size: 20px;
  height: 30px;
`;

export const ErrorDiv = styled.div`
  display: grid;
  grid-auto-columns: 1fr;
  width: 200px;
`;



