import styled from "styled-components";

export const StyledInputBox = styled.input`
  margin: auto;
  width: 250px;
  height: 40px;
  border-radius: 5px;
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-size: 20px;
`;



